import React from "react";
import { Space, Table, Tag } from "antd";

const List = (props) => {
  const { users, onUserSelect123 } = props;
  const columns = [
    {
      title: "Name",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "Action",
      key: "action",
      render: (_, record) => (
        <Space size="middle">
          <a onClick={() => onUserSelect123(record)}>View Details</a>
        </Space>
      ),
    },
  ];
  return <Table columns={columns} dataSource={users} />;
};

export default List;
